@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,500,300,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Navbar */
.navbar-nav li a {
  font-size: 17px;
}

.navbar-brand {
  letter-spacing: 3px;
}

/* Card */
.card {
  transition: box-shadow 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, box-shadow 0.3s;
  transition: transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s;
}

.card:hover {
  -webkit-transform: scale(1.1) rotateY(10deg);
          transform: scale(1.1) rotateY(10deg);
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}

/* Product */
.product img {
  transition: all 0.5s ease;
}

.product img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-center {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}

.nav-item {
  font-size: 30px;
}

.header-center {
  font-size: 35px;
}
* {
  font-family: Open Sans;
}

html,
body {
  height: 90%;
  margin: 0;
  padding: 0;
}


.wrapper {
  min-height: 90%;
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1 1;
  padding-bottom: 100px;
}

section {
  width: 80%;
  display: inline-block;
  background: #333;
  height: 10vh;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  text-decoration: underline;
  position: static;
}

.footer-distributed {
  background: #666;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  position: fixed;
  bottom: 0;
  font: bold 16px sans-serif;
  padding: 20px 50px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}


.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right {
  display: inline-block;
  vertical-align: top;
}

/* Footer left */

.footer-distributed .footer-left {
  width: 40%;
}

/* The company logo */

.footer-distributed h3 {
  color: #ffffff;
  font: normal 36px 'Open Sans', cursive;
  margin: 0;
}

.footer-distributed h3 span {
  color: lightseagreen;
}

/* Footer links */

.footer-distributed .footer-links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 0;
}

.footer-distributed .footer-links a {
  display: inline-block;
  line-height: 1.8;
  font-weight: 400;
  text-decoration: none;
  color: inherit;
}

.footer-distributed .footer-company-name {
  color: #222;
  font-size: 14px;
  font-weight: normal;
  margin: 0;
}

/* Footer Center */
.icon-info2 {
  position: horizontal;
}

.footer-distributed .footer-center .icon-info {
  display: flex;
  align-items: center;
}

.footer-distributed .footer-center .icon-info p {
  margin-left: 10px;
}

.footer-distributed .footer-center {
  width: 35%;
  display: flex;
  justify-content: center;
  margin-right: 20px;
}

.footer-distributed .footer-center div {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.footer-distributed .footer-center i {
  background-color: #33383b;
  color: #ffffff;
  font-size: 25px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  margin-right: 5px;
}

.footer-distributed .footer-center i.fa-envelope {
  font-size: 17px;
  line-height: 38px;
}

.footer-distributed .footer-center p {
  display: inline-block;
  color: #ffffff;
  font-weight: 400;
  vertical-align: middle;
  margin: 0;
}

.footer-distributed .footer-center p span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 2;
}

.footer-distributed .footer-center p a {
  color: lightseagreen;
  text-decoration: none;
  ;
}

.footer-distributed .footer-links a:before {
  content: "|";
  font-weight: 300;
  font-size: 20px;
  left: 0;
  color: #fff;
  display: inline-block;
  padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
  content: none;
}

/* Footer Right */

.footer-distributed .footer-right {
  width: 20%;
  text-align: right;
}


.footer-distributed .footer-company-about span {
  display: block;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 20px;
}

.footer-distributed .footer-icons {
  margin-top: 25px;
}

.footer-distributed .footer-icons a {
  display: inline-block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;

  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;

  margin-right: 3px;
  margin-bottom: 5px;
}

/* If you don't want the footer to be responsive, remove these media queries */

@media (max-width: 880px) {
  .footer-distributed {
    display: none;
    /* Hide the footer */
  }

  .footer-distributed .footer-left,
  .footer-distributed .footer-center,
  .footer-distributed .footer-right {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    /* Decrease the margin-bottom for better spacing */
    text-align: center;
  }

  .footer-distributed .footer-center i {
    margin-left: 0;
  }

  .footer-distributed .footer-center p {
    display: block;
    /* Display text vertically */
  }
}
.custom-controls .carousel-control-prev-icon,
.custom-controls .carousel-control-next-icon {
  height: 100px;
  width: 100px;
  outline: black;
  background-size: 100%, 100%;
  border-radius: 50%;
  border: 1px solid transparent;
  /* Add transparent border */
  background-image: none;
}

.hidden {
  display: none;
}



.custom-controls .carousel-control-next-icon:after {
  content: '>';
  font-size: 55px;
  color: red;
}

.custom-controls .carousel-control-next:hover .carousel-control-next-icon:after {
  content: '>';
  font-size: 55px;
  color: black;
}

.custom-controls .carousel-control-next:hover .carousel-control-next-icon {
  background-color: red;
  border-color: red;
  border: 1px solid black;
  /* Add border on hover */
}

.custom-controls .carousel-control-prev-icon:after {
  content: '<';
  font-size: 55px;
  color: red;
}

.custom-controls .carousel-control-prev:hover .carousel-control-prev-icon:after {
  content: '<';
  font-size: 55px;
  color: black;
}

.custom-controls .carousel-control-prev:hover .carousel-control-prev-icon {
  background-color: red;
  border-color: red;
  border: 1px solid black;
  /* Add border on hover */
}

.custom-down-arrow {
  height: auto;
  width: auto;
  outline: black;
  border: none;
  background: none;
  margin-top: 0px;
  padding-top: 0px;
  margin-bottom: 30px;
  padding-bottom: 30px;
}

.custom-down-arrow.hidden {
  display: none;
}

.discover.hidden {
  display: none;
}

.custom-down-arrow:after {
  margin-top: 0px;
  padding-top: 0px;
  content: '⬇️';
  font-size: 65px;
  margin-bottom: 25px;
}

.carousel-inner {
  transition: 0.5s ease-in-out;
}

carousel-item {
  display: none;
}

carousel-item.active {
  display: block;
}

.discover {
  margin-bottom: -5px;
  padding-bottom: -5px;
  /* Adjust the desired margin value */
}

.custom-down-arrow {
  margin-top: 0px;
  padding-top: 0px;
  margin-bottom: 20px;
  padding-bottom: 60px;
  /* Adjust the desired margin value */
}

.carousel-item {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

@media (max-width: 879px) {
  .carousel-item {
    background-image: ('/assets/images/home/africa.jpg');
  }
}

@media (min-width: 880px) {
  .carousel-item {
    background-image: ('/assets/images/home/purecolor.jpg');
  }
}
.list-group-item {
  list-style: none;
  /* Remove the bullets */
}
